  margin: 20px;
}

.help {
  -webkit-animation: pulse 2s infinite;
          animation: pulse 2s infinite;
}

@-webkit-keyframes pulse {
  0% {
    background-color: #fff;
  }
  100% {
    background-color: #ff4136;
    background-color: var(--color-animation);
  }
}

@keyframes pulse {
  0% {
    background-color: #fff;
  }
  100% {
    background-color: #ff4136;
    background-color: var(--color-animation);
  }
}

